.header {
  margin: 0;
  padding: 40px 0;
  border-bottom: 1px solid rgba(84, 84, 84, 0.7);
  box-sizing: border-box;
  display: flex;
  align-items: center;
  justify-content: space-between;
  z-index: 3;
}

@media (max-width: 450px) {
  .header {
    padding: 22px 11px 25px 11px;
  }
}
